import React, {Component} from "react";
import {loadModule, stopModules} from "eshop/modules/core/utils/ui";

export default class MultiCartItemTooltip extends Component {
    constructor (props) {
        super(props);
    }

    componentDidMount() {
        this.expanderPromise = loadModule(this.priceTooltip, {
            path: "core/modules/tooltips",
            options: {
                triggerSelector: '.o-tooltip__trigger',
                contentSelector: '.o-tooltip__content',
                triggerEvent: 'mouseover',
                maxWidth: '400'
            }
        });
    }

    componentWillUnmount() {
        OPL.UI.stopModules( this.priceTooltip )
    }

    optionalSmallClass(){
        return this.props.small ? " g-icon--font" : ""
    }

    render() {
        return (
            <span ref={(ref) => this.priceTooltip = ref}>
                <span data-type="no-click" data-tooltip-trigger-event="mouseover" className={"o-tooltip__trigger o-tooltip--top g-icon g-icon--only g-icon--hint g-icon--xs-s u-spacing-left-s" + this.optionalSmallClass()}></span>
                <span className="o-tooltip__content">
                    {this.props.children}
                </span>
            </span>
        )
    }
}